* {
  padding: 0;
  margin: 0;
  box-sizing: border-box;
}
.content {
  height: 90vh;
}
.resgister-page {
  display: flex;
  align-items: center;
  justify-content: center;
  height: 100vh;
  background-color: #eeeded;
}

/* transection page */
.filters {
  display: flex;
  align-items: center;
  justify-content: space-between;
  flex-wrap: wrap;
  padding: 1rem 1.5rem;
  box-shadow: 0 0 5px gray;
  border: 1px solid gray;
  /* padding:5px; */
}

/* transection page */

/* antd icons */

.anticon svg {
  font-size: 20px;
  cursor: pointer;
}
.active-icon {
  color: black;
}
.inactive-icon {
  color: gray;
}

@media only screen and (max-width: 700px) {
  .add_trans {
    margin-top: 1rem;
  }
}

@media only screen and (min-width: 768px) {
  switch-icons {
    border: 1px solid rgba(0, 0, 0, 0.677);
    border-radius: 5px;
    padding: 10px 15px;
  }
  .analytic_sec{
    display:flex;
    flex-wrap: nowrap;
  }
}
